<template>
  <div class="admin-nav-wrapper">
    <Spinner v-if="isLoading === true" />
    <div v-else>
      <div class="admin-navbar">
        <div class="nav-brand">
          <router-link to="/">
            <img class="logo-img" src="https://imgpile.com/images/biexQR.png" alt="" />
          </router-link>
        </div>

        <div class="item-group d-flex">
          <div class="item-group-title"></div>
          <button class="btn-login" v-if="isAuthenticated" @click="logout">
            
          </button>

          <button class="btn-login" v-else>
            <router-link to="/signin" class="routerLink">
              
            </router-link>
          </button>


        </div>
      </div>
    </div>
  </div>
</template>
  
<script>
import { mapState } from "vuex";
import Spinner from "./Spinner.vue";


export default {
  components: {
    Spinner
  },
  data() {
    return {
      isLoading: true
    }
  },
  created() {
    this.isLoading = false
  },
  methods: { 
    logout() {
      this.$store.commit("revokeAuthentication");
      this.$router.push("/signin");
    },
  },
  computed: {
    ...mapState(["currentUser", "isAuthenticated"]),
  },
};
</script>
  
<style lang="scss">
.admin-nav-wrapper {
  
}

.admin-navbar {
  position: fixed;
  top: 0;
  width: 100%;
  height: 72px;
  line-height: 72px;
  margin: auto;
  box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.06);
  background: #ffffff;
  z-index: 9;
  @media (min-width: 768px) {
    position: relative;
  }

  .nav-brand {
    position: absolute;
    top: 50%;
    left: 27%;
    transform: translate(-50%, -50%);

    @media (min-width: 768px) {
      left: 20%;
    }

    .logo-img {
      width: 10rem;
    }
  }

  .item-group {
    justify-content: space-around;
    position: absolute;
    width: 10rem;
    line-height: 40px;
    top: 50%;
    right: 2.5rem;
    transform: translateY(-50%);

    &-title {
      font-weight: 600;
    }

    .routerLink {
      text-decoration: none;
    }

    .btn-login {

      display: flex;
      background: #ffffff;
      border-radius: 5px;
      border: 1px solid #747373;

    }

    img {
      height: 25px;
    }

    .btn-search {
      @media (min-width: 768px) {
        display: none;
      }
    }

  }
}
</style>