<template>
  <div class="orderListCard">
    <ProcessSpinner 
    v-if="isProcessing === true"
    />
    <table class="orderList-table">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td class="orderList-table-item-id">{{ order.id }}</td>
          <td class="orderList-table-item-date">{{ order.createdAt }}</td>
          <td class="orderList-table-item-amount">NT$ {{ order.amount }}</td>
          <td class="orderList-table-item-payment">
            
            <GetPaymentModal 
            :initial-order="order"
            v-if="order.payment_status === '0'"
            />

            <div v-else-if="order.payment_status === '1'"></div>
            <div v-else></div>
              

        
          </td>
          <td class="orderList-table-item-shipment">
            <div v-if="order.shipping_status === '0'"></div>
            <div v-else-if="order.shipping_status === '1'"></div>
            <div v-else></div>
          </td>
          <td class="orderList-table-item-others">
            <OrderModal 
            :initial-order="order"/>
            
          </td>
          <td class="orderList-table-item-cancel">
            <div v-if="order.payment_status === '-1'"></div>
            <div
              class="orderList-table-item-cancel-btn"
              v-else
              @click.prevent.stop="cancelOrder"
            >
              
            </div>
          </td>
        </tr>
      </tbody>
    </table>
    <div class="info-table mt-2">
      <table>
        <tbody>
          <div
            class="detail-show-btn"
            style="display: block"
            v-if="isSwitching === false"
          >
            <img
              class="detailSwitch"
              @click.prevent.stop="handleSwitch"
              src="//d.ecimg.tw/img/ecvip/img2/icon_list1.gif"
              alt="+"
            />
            <div class="detail-wrapper"></div>
          </div>
          <div class="detail-hidden-btn" v-else>
            <img
              class="detailSwitch"
              @click.prevent.stop="handleSwitch"
              src="//d.ecimg.tw/img/ecvip/img2/icon_list0.gif"
              alt="-"
            />
            <div class="detail-wrapper">
              <table>
                <tbody
                  v-for="orderProduct in order.orderProducts"
                  :key="orderProduct.id"
                >
                  <tr>
                    <td class="detail-wrapper-item-image">
                      <img :src="orderProduct.image" alt="" />
                    </td>
                    <td class="detail-wrapper-item-name">
                      {{ orderProduct.name }}
                    </td>
                    <td class="detail-wrapper-item-quantity">
                      {{ orderProduct.OrderItem.quantity }} 
                    </td>
                    <td class="detail-wrapper-item-price">
                      NT$
                      {{ orderProduct.OrderItem.quantity * orderProduct.price }}
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </tbody>
      </table>
    </div>
    <hr />
  </div>
</template>

<script>
import OrderApi from "../apis/order";
import OrderModal from"../components/OrderModal.vue"
import GetPaymentModal from "../components/GetPaymentModal.vue"
import { Toast } from "../utils/helpers";
import ProcessSpinner from "./ProcessSpinner.vue";

export default {
    components: {
        OrderModal,
        GetPaymentModal,
        ProcessSpinner

    },
  props: {
    initialOrder: {
      type: Object,
      required: true
    },
  },
  data() {
    return {
      order: this.initialOrder,
      orderId: this.initialOrder.id,
      modal: null,
      tradeInfo: [],
      isProcessing: false,
      isSwitching: false
    };
  },
  created() {
  },
  methods: {
    async cancelOrder() {
      try {
        this.isProcessing = true
        const response = await OrderApi.cancelOrder({
          orderId: this.orderId
        })
        if(response.status === 'error') {
          throw new Error (response.data.message)
        }
        this.order.shipping_status = -1
        this.order.payment_status = -1

        this.isProcessing = false
      } catch (error) {
        this.isProcessing = false
        Toast.fire({
          icon: 'error',
          title: ''
        })
      }
    },
    handleSwitch() {
     this.isSwitching = !this.isSwitching
    }
  },
};
</script>

<style lang="scss">
.orderListCard {
  position: relative;
}
</style>