<template>
  <div class="home-container">
    <HomeSwiper></HomeSwiper>
    <div class="container">
      <div class="m-3">
      
      <div class="about-banner row d-flex mt-5" data-aos="slide-down">
        <div class="col-md-6">
          <h2 class=" border-primary pl-3 text-left mt-5">About</h2>
          <p class="m-5">20232</p>
        </div>
        <div class="col-md-6">
          <img src="https://hinetcdn.waca.ec/uploads/shops/14796/products/1b/1bf24b1592e9840e68771bcca8708f38.jpg" alt="" >
        </div>
        
       
      </div>
    </div>
    <div class="cards-wrapper m-3" data-aos="fade-up">
      <h2 class=" border-primary pl-3 text-left  mt-5"></h2>
      <div class="row m-3">
        <div class="product-card col-xl-3 col-lg-4 col-sm-6" v-for="product in products" :key="product.id">
          <router-link :to="{ name: 'product', params: { id: product.id } }">
            <div class="product-img"><img :src="product.image" alt="" /></div>
            <div class="text-left pl-1">
              <div class="product-name">{{ product.name }}</div>
              <span class="product-category"><i class="fa fa-tag" aria-hidden="true"></i>
                {{ product.Category.name }}</span>
              <div class="product-price">NT$ {{ product.price }}</div>
              <div class="card-detail"><i class="fa fa-search" aria-hidden="true"></i>
                </div>
              <button type="button" class="btn p-2 text-white" @click.prevent.stop="addToCart(product.id)"></button>
            </div>

          </router-link>

        </div>
      </div>

    </div>
    <div class="message-wrapper text-left  m-3" data-aos="slide-right">
      <h2 class="border-primary pl-3 mt-5"></h2>
      <div class="row d-flex justify-content-center">
        <div class="col-lg-4 mb-5" v-for="message in messages" :key="message.id">
          <div class="message-card bg-light m-2 p-2">
            <h4 class="message-title mt-3">{{ message.product }}</h4>
            <div class="d-flex align-items-center">
              <img :src="message.avatar" alt="">
              <div class="ml-2 mt-3">
                <p class="message-name mt-3">{{ message.name }}</p>
                <p class="mt-3">{{ message.feedback }}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    </div>
    
  </div>
</template>

<script>
import ProductsApi from '../apis/products'
import CartApi from '../apis/cart'
import HomeSwiper from '../components/HomeSwiper.vue';
import { Toast } from "../utils/helpers";
import { mapState } from "vuex";

const dummyData = [
      {
        id: 1,
        avatar: 'https://w7.pngwing.com/pngs/754/2/png-transparent-samsung-galaxy-a8-a8-user-login-telephone-avatar-pawn-blue-angle-sphere-thumbnail.png',
        name: '',
        product: '',
        feedback: ''
      },
      {
        id: 2,
        avatar: 'https://w7.pngwing.com/pngs/754/2/png-transparent-samsung-galaxy-a8-a8-user-login-telephone-avatar-pawn-blue-angle-sphere-thumbnail.png',
        name: '',
        product: '',
        feedback: ''
      },
      {
        id: 3,
        avatar: 'https://w7.pngwing.com/pngs/754/2/png-transparent-samsung-galaxy-a8-a8-user-login-telephone-avatar-pawn-blue-angle-sphere-thumbnail.png',
        name: '',
        product: '',
        feedback: ''
      }
    ]


export default {
  name: 'HomeView',
  components: {
    HomeSwiper

  },
  data() {
    return {
      products: [],
      clickProductId: '',
      messages: ''

    }
  },
  created() {
    this.getProducts();
    this.messages = dummyData
  },
  methods: {
    async getProducts() {
      try {
        const res = await ProductsApi.getProducts({
          page: "",
          categoryId: ""
        })
        const data = res.data.products.splice(0, 4)
        this.products = data 
      } catch (error) {
        console.log(error)
      }
    },
    async addToCart(id) {
      try {
        this.clickProductId = id
        this.isProcessing = true

        const response = await CartApi.postCart({
          productId: id,
          quantity: 1,
          cartId: this.cartId
        })
        const { data } = response;
        this.$bus.$emit("cartUpdate", {
          cart: data.cart,
        });
        if (response.status === 200) {
          Toast.fire({
            icon: "success",
            title: "",
          });
        }
        if (response.status === "error") {
          throw new Error(data.message);
        }
        await this.getCart();
        this.isProcessing = false
      } catch (error) {
        this.isProcessing = false
        if (this.isAuthenticated === false) {
          Toast.fire({
            icon: "error",
            title: "",
          });
        } else {
          Toast.fire({
            icon: "error",
            title: "",
          });
        }
      }
    },
    async getCart() {
      try {
        const response = await CartApi.getCart();
        const { data } = response;
        if (response.status === "error") {
          throw new Error(data.message);
        }
        this.$bus.$emit("cartUpdate", {
          cart: data.carts,
        });
      } catch (error) {
        console.log(error);
      }
    },
  },
  computed: {
    ...mapState(["currentUser", "isAuthenticated"]),
  },
}
</script>

