<template>
    <div class="container-wrapper mt-5">
        <div class="row container m-auto">
            <div class="col-lg-3 sidebar mt-5 mt-md-0">
                <h2 class="bg-light text-left pl-3"></h2>
                <ul class="mt-3 d-none d-lg-block">
                    <li v-for="item in data" :key="item.id" @click="handleSelected(item.id)"
                        :class="{ active: item.id === secectedId }" class="list-item mb-3">{{ item.title }}</li>
                </ul>
                <div class="select-wrapper mt-3 d-lg-none text-left mb-3 ">
                    <select class="border-0" name="" id=""
                    @change="handleChange"
                    >
                        <option :value="item.id"
                        v-for="item in data"  :key="item.id" 
                        :class="{ active: item.id === secectedId }"
                        >{{ item.title }}</option>
                    </select>
                </div>
            </div>
            <div class="col-lg-9">
                <div>
                    <img src="https://media.istockphoto.com/id/1337475990/zh/%E7%85%A7%E7%89%87/q-and-a-question-and-answer-shot-form-on-wooden-block.jpg?s=612x612&w=0&k=20&c=NxC0VWqfwLo8XLi093j8WqI8Tk5j-uOzOiVs_nwdYuo="
                        alt="">
                </div>
                <ul class="mt-3">
                    <li class="question-title text-left mb-3" v-for="item in selectedData.questions" :key="item.id"
                    @click="goToQuestion(item.id)"
                    >Q{{
                        item.id }}.{{ item.title }}</li>
                </ul>
                <hr>
                <div>
                    <div class="text-left" v-for="item in selectedData.questions" :key="item.id" :id="item.id">
                        <h6 class="question-title mt-3">Q{{ item.id }}.{{ item.title }}</h6>
                        <p class="question-answer mt-3">{{ item.answer }}</p>
                        <p class="text-danger mt-3">{{ item.alert }}</p>
                        <hr>

                    </div>

                </div>
            </div>
        </div>
    </div>
</template>

<script>
import $ from "jquery";

const dummyData = [
    {
        id: 1,
        title: '',
        questions: [
            {
                id: 1,
                title: '',
                answer: '',
                alert: ' '
            },
            {
                id: 2,
                title: '?',
                answer: '',
                alert: ''
            },
            {
                id: 3,
                title: '',
                answer: '',
                alert: ''
            }
        ]
    },
    {
        id: 2,
        title: '',
        questions: [
            {
                id: 1,
                title: '?',
                answer: '',
                alert: ' 3()'
            },
            {
                id: 2,
                title: '?',
                answer: `
                   1.120060`,
                alert: ''
            },
            {
                id: 3,
                title: '?',
                answer: '3-7',
                alert: ''
            }
        ]
    },
    {
        id: 3,
        title: '',
        questions: [
            {
                id: 1,
                title: '?',
                answer: `
                
                
                
                7
                
                `,
                alert: ''
            },
            {
                id: 2,
                title: '',
                answer: ` 7-8
                
                 7~14`,
                alert: ''
            },
           
        ]
    },
    {
        id: 4,
        title: '',
        questions: [
            {
                id: 1,
                title: '',
                answer: `

`,
                alert: ''
            },
            {
                id: 2,
                title: '?',
                answer: '',
                alert: ''
            },
            {
                id: 3,
                title: '?',
                answer: '',
                alert: ''
            }
        ]
    }
]
export default {

    data() {
        return {
            data: dummyData,
            secectedId: 1,
            selectedData: {}
        }
    },
    created() {
        this.selectedData = dummyData.filter(item => item.id === this.secectedId)[0]
    },
    methods: {
        handleSelected(id) {
            this.secectedId = id
            this.selectedData = dummyData.filter(item => item.id === this.secectedId)[0]
        },
        handleChange(e) {
            this.secectedId = e.target.value
            this.selectedData = this.selectedData = dummyData.filter(item => item.id === Number(e.target.value))[0]
        
        },
        goToQuestion(id) {
            const height = $(`#${id}`).offset()?.top
        $('html, body').animate({
            scrollTop: height,
        }, 200)

        }
    }
}
</script>

<style lang="scss">
.container-wrapper {
    min-height: calc(100vh - 100px);

    img {
        height: 300px;
        object-fit: cover;
    }

    .sidebar {
        h2 {
            border-left: 5px solid #00457c;
        }
        .list-item {
            font-weight: 600;

            &:hover {
                cursor: pointer;
                background: #00457c;
                color: #fff;
            }

            &.active {
                background: #00457c;
                color: #fff;

            }
        }
        .select-wrapper {
            background-color: #00457c;
            padding: 0.5rem;
            select {
                color: #fff;
                background-color: #00457c;
                font-weight: 600;
            }
        }
    }

    .question-title {
        font-weight: 600;
        &:hover {
            cursor: pointer;
        }
    }

    .question-answer {
        white-space: pre-line;
        line-height: 2rem;
    }
}
</style>