<template>
    <div class="go-top d-flex justify-content-center align-items-center"
    @click.prevent.stop="GoTop"
    >
        <i class="fa fa-arrow-up" aria-hidden="true"></i>
    </div>
</template>

<script>
import $ from "jquery";

export default {
    data() {
        return {
            height: '',
            scrollTop: ''
        }

    },
    created() {
        this.height = $(window).height()
        window.addEventListener("scroll", this.handleScroll)
    },
     methods: {
        GoTop() {
       
            $('html, body').animate({
            scrollTop: 0,
        }, 200)
        },
        handleScroll() {
            const scrollTop = $(window).scrollTop();
            this.scrollTop = scrollTop
            if (scrollTop > this.height / 2) {
                $('.go-top').css('opacity', '0.9');

            } else {
                $('.go-top').css('opacity', '0');
            }
        }
    }
    
}
</script>

<style lang="scss">
.go-top {
    position: fixed;
    bottom: 1.5rem;
    right: 1.5rem;
    height: 4rem;
    width: 4rem;
    font-size: 2rem;
    background-color: #e6dfdf;
    border-radius: 50%;
    box-shadow: 0 0 0 1px rgba(0, 0, 0 ,0.3);
    cursor: pointer;
    opacity: 0;
    transition: all 0.3s ease-in;
}

</style>