<template>
  <div class="products-container container">
    <Spinner v-if="isLoading === true" />
    <div class="products-wrapper row">
      <div class="side-wrapper col-md-3">
        <div class="title mt-md-0 mt-5 bg-light text-left pl-3"></div>
        <ul class="list-group d-none d-md-block mt-3">
          <li class="list-item"
          :class="{ active: categoryId === '' }"
          >
            <router-link to="/products"><i class="fas fa-angle-left"></i>
            
            </router-link>
          </li>
          <li class="list-item" v-for="category in categories" :key="category.id"
            :class="{ active: categoryId === category.id }">
            <router-link :to="{ name: 'products', query: { categoryId: category.id } }">
              <i class="fas fa-angle-left"></i>
              {{ category.name }}
            </router-link>
          </li>
        </ul>
        <div class="select-wrapper text-left d-md-none pl-3 mt-3 mb-3">
          <select class="border-0" name="" id=""
          @change="handleChange"
          > 
          <option value=""></option>
            <option :value="category.id" v-for="category in categories" :key="category.id"
              :class="{ active: category.id === categoryId }"
              >{{ category.name }}</option>
          </select>
        </div>
      </div>
      <div class="main-wrapper col-md-9" v-if="products.length">
        <div class="product-wrapper">
          <ProductSwiper :products="products" />
          <div class="product-wrapper">
            <h2 class="title"> TOP 5</h2>
            <div class="product-card" v-for="(item, index) in products.slice(0, 5)" :key="item.id"
              @click.prevent.stop="$router.push(`/products/${item.id}`)">
              <div class="product-card-number">0{{ index + 1 }}</div>
              <div class="product-card-image">
                <img :src="item.image" alt="" />
              </div>
              <div class="product-card-name">{{ item.name }}</div>
            </div>
          </div>
        </div>
        <h2 class="title"></h2>
        <hr>
        <div class="cards-wrapper position-relative">
          <div class="product-card mb-3" v-for="product in products" :key="product.id">
            <ProcessSpinner v-if="isProcessing === true && ClickProductId
              === product.id" />
            <router-link :to="{ name: 'product', params: { id: product.id } }">
              <div class="product-img"><img :src="product.image" alt="" /></div>
              <div class="text-left pl-1">
                <div class="product-name">{{ product.name }}</div>
                <span class="product-category"><i class="fa fa-tag" aria-hidden="true"></i>
                  {{ product.Category.name }}</span>
                <div class="product-price">NT$ {{ product.price }}</div>
                <div class="product-detail"><i class="fa fa-search" aria-hidden="true"></i>
                  </div>
                <button type="button" class="btn p-2 text-white"
                  @click.prevent.stop="addToCart(product.id)"></button>
              </div>

            </router-link>
          </div>
        </div>
        <ProductsPagination :current-page="currentPage" :total-page="totalPage" :previous-page="previousPage"
          :next-page="nextPage" :categoryId="categoryId" />
      </div>
      <h2 class="border-0 mt-3 ml-5" v-else></h2>

    </div>
  </div>
</template>

<script>
import ProductsPagination from "../components/ProductsPagination.vue";
import ProductsApi from "../apis/products";
import CartApi from "../apis/cart"
import { Toast } from "./../utils/helpers";
import ProductSwiper from "../components/ProductSwiper.vue";
import Spinner from "../components/Spinner.vue";
import ProcessSpinner from "../components/ProcessSpinner.vue";

export default {
  components: {
    ProductsPagination,
    ProductSwiper,
    Spinner,
    ProcessSpinner
  },

  data() {
    return {
      products: [],
      categories: [],
      categoryId: "",
      currentPage: 1,
      totalPage: 1,
      previousPage: -1,
      nextPage: -1,
      isLoading: true,
      isProcessing: false,
      ClickProductId: ''
    };
  },
  created() {
    const { page = "", categoryId = "" } = this.$route.query;
    this.fetchProducts({
      queryPage: page,
      queryCategoryId: categoryId,
    });
    this.cartId = localStorage.getItem("cartId");
  },
  beforeRouteUpdate(to, from, next) {
    const { page = "", categoryId = "" } = to.query;
    this.isLoading = true;
    this.fetchProducts({ queryPage: page, queryCategoryId: categoryId });
    next();
  },
  methods: {
    async fetchProducts({ queryPage, queryCategoryId }) {
      try {
        const response = await ProductsApi.getProducts({
          page: queryPage,
          categoryId: queryCategoryId,
        });
        const { data } = response;
        if (response.status === "error") {
          throw new Error(data.message);
        }

        const { products, categories, categoryId, pagination } = data;
        this.products = products;
        this.categories = categories;
        this.categoryId = categoryId;
        const { currentPage, totalPage, prev, next } = pagination;
        this.currentPage = currentPage;
        this.totalPage = totalPage;
        this.previousPage = prev;
        this.nextPage = next;

        this.isLoading = false;
      } catch (error) {
        Toast.fire({
          icon: "error",
          title: "",
        });

      }
    },
    async addToCart(id) {
      try {
        this.ClickProductId = id
        this.isProcessing = true
        const response = await CartApi.postCart({
          productId: id,
          quantity: 1,
          cartId: this.cartId
        })
        const { data } = response;
        this.$bus.$emit("cartUpdate", {
          cart: data.cart,
        });
        if (response.status === 200) {
          Toast.fire({
            icon: "success",
            title: "",
          });
        }
        if (response.status === "error") {
          throw new Error(data.message);
        }
        await this.getCart();
        this.isProcessing = false
      } catch (error) {
        console.log(error)
        this.isProcessing = false
        Toast.fire({
          icon: "error",
          title: "",
        });
      }
    },
    async getCart() {
      try {
        const response = await CartApi.getCart();
        const { data } = response;
        if (response.status === "error") {
          throw new Error(data.message);
        }
        this.$bus.$emit("cartUpdate", {
          cart: data.carts,
        });
      } catch (error) {
        console.log(error);
      }
    },
    async handleChange(e) {
      this.isLoading = true
      this.categoryId = e.target.value
      await this.fetchProducts({queryPage: "", queryCategoryId:this.categoryId })
      this.isLoading = false
    }
  },
};
</script>

