<template>
    <swiper class="swipe-container" ref="mySwiper" :options="swiperOptions">
        <swiper-slide class="swiper-slide"><img
                src="https://www.sweet-dumpling.com/media/content/4e6846c86858d8ac69c3e236b4416540/macaron-cover.jpg"
                alt=""></swiper-slide>
        <swiper-slide class="swiper-slide"><img
                src="https://tokyo-kitchen.icook.network/uploads/recipe/cover/81654/ecdf702af6a7b08c.jpg"
                alt=""></swiper-slide>
        <swiper-slide class="swiper-slide"><img src="https://www.love2gather.co/content/images/2021/08/product--138.jpg"
                alt=""></swiper-slide>

    </swiper>
</template>

<script>
export default {
    data() {
        return {
            swiperOptions: {
                centeredSlides: true,
                autoplay: {
                    disableOnInteraction: false,
                    delay: 2500,
                },
                observer: true,
                observeSlideChildren: true,

                preventLinksPropagation: false,
                pagination: {
                    el: ".swiper-pagination",
                    clickable: true,
                    renderBullet: function (index, className) {
                        return '<span class="' + className + '">' + (index + 1) + "</span>";
                    },
                },
            },
        }
    },
    computed: {
        swiper() {
            return this.$refs.mySwiper.$swiper;
        },
    },
}
</script>

<style lang="scss">
.swipe-container {
    img {
        object-fit: cover;
        height: 440px;

        @media (min-width: 768px) {
            height: 550px;

        }
    }
}</style>