<template>
  <div class="search-container container">
    <Spinner v-if="isLoading === true" />
    <div class="products-wrapper row" v-else>

      <div class="side-wrapper col-md-3">
        <div class="title bg-light text-left pl-3"></div>

        <ul class="list-group d-md-block d-none">
          <li class="list-item  d-md-block d-none mt-3" :class="{ active: categoryId === '' }" @click="changeCategroyId()">
            <i class="fas fa-angle-left"></i>
            <span> </span>
          </li>
          <li class="list-item" v-for="category in categories" :key="category.id"
            :class="{ active: categoryId === category.id }" @click="changeCategroyId(category.id)">
            <i class="fas fa-angle-left"></i>
            {{ category.name }}

          </li>
        </ul>
        <div class="select-wrapper text-left d-md-none pl-3 mt-3 mb-3">
          <select class="border-0" name="" id="" @change="handleChange">
            <option value=""></option>
            <option v-for="category in categories" :key="category.id" :value="category.id">{{ category.name }}</option>
          </select>
        </div>

      </div>
      <div class="main-wrapper col-md-9">
        <div class="title">
          {{ number }}"{{ `${$route.query.key}` }}"
        </div>
        <div class="cards-wrapper">
          <div class="product-card" v-for="product in products" :key="product.id">
            <router-link :to="{ name: 'product', params: { id: product.id } }">
              <div class="product-img">
                <img :src="product.image" alt="" />
              </div>
              <div class="text-left pl-1">
                <div class="product-name">{{ product.name }}</div>
                <span class="product-category"><i class="fa fa-tag" aria-hidden="true"></i>
                  {{ product.Category.name }}</span>
                <div class="product-price">NT$ {{ product.price }}</div>
                <div class="product-detail"><i class="fa fa-search" aria-hidden="true"></i>
                  </div>
                <button type="button" class="btn p-2 text-white"
                  @click.prevent.stop="addToCart(product.id)"></button>
              </div>
            </router-link>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ProductsApi from "../apis/products";
import CartApi from "../apis/cart"
import Spinner from "../components/Spinner.vue";
import { Toast } from "../utils/helpers";

export default {
  components: {
    Spinner,
  },
  data() {
    return {
      products: [],
      categories: [],
      categoryId: '',
      ClickProductId: '',
      number: "",
      isLoading: true,
    };
  },
  mounted() {

    this.fetchProducts();
    this.fetchCategories();
  },
  beforeRouteUpdate(to, from, next) {
    next();
    this.fetchProducts();
  },
  methods: {
    async fetchProducts() {
      try {
        this.isLoading = false;
        const response = await ProductsApi.getProducts({
          categoryId: this.categoryId,
          page: "",
          keyword: this.$route.query.key,
        });

        const { data } = response;
        if (response.status === "error") {
          throw new Error(data.message);
        }

        this.products = response.data.totalProducts;
        this.number = response.data.totalProducts.length;
        this.isLoading = false;
      } catch (error) {
        Toast.fire({
          icon: "error",
          title: "",
        });

        this.isLoading = false;
      }
    },
    async fetchCategories() {
      try {
        const res = await ProductsApi.getCategories()
        this.categories = res.data.categories
      } catch (error) {
        console.log(error)
      }
    },
    async changeCategroyId(id) {
      if (!id) {
        this.categoryId = ''
      } else {
        this.categoryId = id
      }
      await this.fetchProducts()

    },
    async addToCart(id) {
      try {
        this.ClickProductId = id

        const response = await CartApi.postCart({
          productId: id,
          quantity: 1,
          cartId: this.cartId
        })
        const { data } = response;
        this.$bus.$emit("cartUpdate", {
          cart: data.cart,
        });
        if (response.status === 200) {
          Toast.fire({
            icon: "success",
            title: "",
          });
        }
        if (response.status === "error") {
          throw new Error(data.message);
        }
        await this.getCart();
        this.isProcessing = false
      } catch (error) {
        this.isProcessing = false
        Toast.fire({
          icon: "error",
          title: "",
        });
      }
    },
    async getCart() {
      try {
        const response = await CartApi.getCart();
        const { data } = response;
        if (response.status === "error") {
          throw new Error(data.message);
        }
        this.$bus.$emit("cartUpdate", {
          cart: data.carts,
        });
      } catch (error) {
        console.log(error);
      }
    },
    async handleChange(e) {
      this.isLoading = true
      this.categoryId = e.target.value
      await this.fetchProducts({ queryPage: "", queryCategoryId: this.categoryId })
      this.isLoading = false
    }
  },
};
</script>
