<template>
  <div class="nav-wrapper">
    <Spinner v-if="isLoading === true" />
    <div v-else>
      <div class="navbar">
        <div class="nav-brand">
          <router-link to="/">
            <img class="logo-img" src="https://imgpile.com/images/biexQR.png" alt="" />
          </router-link>
        </div>
        <div class="search-bar collapse" id="search-bar">
          <div class="input-wrapper d-flex">
            <div class="input-icon">
              <i class="fas fa-search"></i>
            </div>

            <input type="text" class="form-control search-input" placeholder="" v-model.lazy="keyword"
              @change="searchKeyword" />
            <button class="form-control btn-search"></button>
            <button class="close pl-3" @click.prevent.stop="closeCollapse('search-bar')">
              <i class="fa fa-times"></i>
            </button>
          </div>
          <div class="bottom" @click="closeCollapse('search-bar')"></div>
        </div>
        <div class="item-group d-flex">
          <button class="btn-login" v-if="isAuthenticated" @click="logout">
            
          </button>

          <button class="btn-login" v-else>
            <router-link to="/signin" class="routerLink">  </router-link>
          </button>
          <button class="search-icon" type="button" data-bs-toggle="collapse" data-bs-target="#search-bar"
            @click.prevent.stop="showCollapse('search-bar')">
            <i class="fas fa-search fa-lg">50%</i>
          </button>
          <button v-show="isAuthenticated">
            <router-link to="/order">
              <i class="fa fa-file-text fa-lg">50%</i>
            </router-link>
          </button>

          <div class="cart-icon">
            <button>
              <router-link to="/cart">
                <i class="fa fa-shopping-cart fa-lg">80%</i>
                <div class="cart-item-wrapper" v-show="carts.length">
                  <span class="item-quantity">{{ carts.length }}</span>
                </div>
              </router-link>

            </button>
          </div>

          <label class="navbar-toggle-label" data-bs-toggle="collapse" data-bs-target="#navbar-expand"
            @click.prevent.stop="showCollapse('navbar-expand')">
            <span class="hamburger"></span>
          </label>
        </div>
      </div>
      <div class="topList">
        <router-link to="/asked_questions"  class="router-link mr-3">&</router-link>
        <router-link to="/products" class="router-link mr-3"
          :class="{ active: categoryId === '' && this.$route.name === 'products' }"></router-link>
        <div v-for="category in categories" :key="category.id">
          <router-link :to="`/products?categoryId=${category.id}`" :class="{ active: categoryId === category.id }">{{
            category.name
          }}</router-link>
        </div>
      </div>

      <div class="navbar-expand-collapse collapse" id="navbar-expand">
        <div class="expand-wrapper">
          <div class="expand-item-wrapper">
            <router-link to="/" class="expand-item">
              
            </router-link>
          </div>
          <div class="expand-item-wrapper">
            <router-link to="/asked_questions" class="expand-item">&</router-link>
          </div>
          <div class="expand-item-wrapper login" v-if="!isAuthenticated">
            <router-link to="/signin" class="expand-item">
              /
            </router-link>
          </div>
          <div class="expand-item-wrapper logout" v-else>
            <a href="" class="expand-item" @click="logout">  </a>
          </div>
          <div class="category-group">
            <div class="expand-item-wrapper" @click.prevent.stop="handleChecked">
              <router-link to="/products" class="expand-item">
                

                <i class="fa fa-angle-right"></i>
              </router-link>
            </div>
            <div class="category-list">
              <div class="expand-item-wrapper" v-for="category in categories" :key="category.id">
                <router-link :to="`/products?categoryId=${category.id}`" class="expand-item">
                  {{ category.name }}
                  <i class="fa fa-angle-right"></i>
                </router-link>
              </div>
            </div>
          </div>
        </div>
        <div class="expand-bottom" @click.prevent.stop="closeCollapse"></div>
      </div>

    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { Collapse } from "bootstrap";
import ProductsApi from "../apis/products";
import CartApi from "../apis/cart";
import Spinner from "./Spinner.vue";


export default {
  components: {
    Spinner

  },
  data() {
    return {
      keyword: "",
      collapse: null,
      categories: [],
      carts: [],
      isLoading: true,
      categoryId: ''

    };
  },
  created() {
    this.categoryId = Number(this.$route.query.categoryId) || ''
    this.$bus.$on('cartUpdate', event => {
      this.carts = event.cart
    })

  },
  mounted() {
    this.fetchCategories();
  },
  methods: {
    async fetchCategories() {
      try {
        const response = await ProductsApi.getCategories();
        this.categories = response.data.categories;
        this.fetchCart()
      } catch (error) {
        console.log(error);
      }
    },
    async fetchCart() {
      try {
        this.isLoading = false
        const response = await CartApi.getCart();
        const { data } = response;
        if (response.status === "error") {
          throw new Error(data.message);
        }
        this.isLoading = false
        this.$bus.$emit('cartUpdate', {
          cart: data.carts
        })

      } catch (error) {
        console.log(error);
      }
    },
    handleChecked() {
      if (this.isChecked === true) {
        this.isChecked = false;
      } else {
        this.isChecked = true;
      }
    },

    logout() {
      this.$store.commit("revokeAuthentication");
      this.$router.push("/signin");

    },
    searchKeyword() {
      this.$router.push(`/search?key=${this.keyword}`);
    },
    showCollapse(id) {
      this.collapse = new Collapse(document.getElementById(id));
      this.collapse.show();
    },
    closeCollapse() {
      this.collapse.hide();
    },

  },
  computed: {
    ...mapState(["currentUser", "isAuthenticated"]),
  },
  watch: {
    $route() {
      if (this.collapse) {
        this.collapse.hide();
      }
      this.categoryId = Number(this.$route.query.categoryId) || ''
      this.keyword = "";
    },
  },
};
</script>

<style lang="scss">
.nav-wrapper {
  position: relative;
  margin-top: 3rem;
  box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.06);

  @media (min-width: 768px) {
    margin: auto;
  }
}

.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  height: 72px;
  line-height: 72px;
  margin: auto;
  background: #ffffff;
  z-index: 9;
  box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.06);

  @media (min-width: 768px) {
    position: relative;
    box-shadow: none;

  }

  .nav-brand {
    position: absolute;
    top: 50%;
    left: 27%;
    padding-left: 1rem;
    transform: translate(-50%, -50%);

    @media (min-width: 768px) {
      left: 20%;
    }

    .logo-img {
      width: 10rem;
      object-fit: cover;
    }
  }

  .search-bar {
    transition: all 0.3s ease-in-out;
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 999;

    .input-icon {
      position: absolute;
      top: 0;
    }

    .bottom {
      position: fixed;
      opacity: 0.7;
      width: 100%;
      height: 100%;
      background: #747373;
    }

    .btn-search {
      display: none;
    }

    .input-wrapper {
      padding: 1rem;
      background: #ffffff;
    }

    .search-input {
      border-radius: 0;
      margin-left: 1.5rem;
    }

    @media (min-width: 768px) {
      display: flex;
      position: absolute;
      height: 40px;
      width: 50%;
      top: 0.3rem;
      left: 35%;
      background-color: white;
      z-index: 0;

      .bottom {
        display: none;
      }

      .input-icon {
        display: none;
      }

      .search-input {
        width: 70%;
        margin-left: 0;
      }

      .btn-search {
        display: block;
        width: 70px;
        border-radius: 0;
        background: #00457c;
        color: #ffffff;
      }

      .close {
        display: none;
      }

      .btn-search:hover {
        cursor: pointer;
      }
    }
  }

  .item-group {
    display: flex;
    justify-content: space-evenly;
    position: absolute;
    width: 7rem;
    line-height: 40px;
    right: 2.5rem;

    @media (min-width: 768px) {
      width: 10rem;

    }

    .routerLink {
      text-decoration: none;
    }

    .btn-login {
      display: none;

      @media (min-width: 768px) {
        display: flex;
        margin-right: 1rem;
        background: #ffffff;
        border-radius: 5px;
        border: 1px solid #747373;
      }
    }

    img {
      height: 25px;
    }

    .search-icon {
      @media (min-width: 768px) {
        display: none;
      }
    }

    .btn-search {
      @media (min-width: 768px) {
        display: none;
      }
    }

    .cart-icon {
      position: relative;
      height: 100%;
      text-align: center;

      .cart-item-wrapper {
        .item-quantity {
          position: absolute;
          top: 0;
          background-color: #f16c5d;
          color: #ffffff;
          border-radius: 50%;
          width: 1rem;
          height: 1rem;
          line-height: 1rem;
          vertical-align: middle;
          font-size: 0.6rem;
        }
      }
    }

    .navbar-toggle-label {
      display: flex;
      width: 25px;
      margin-top: 0.5rem;
      text-align: center;

      @media (min-width: 768px) {
        display: none;
      }

      .hamburger {
        height: 3px;
        width: 20px;
        margin: auto;
        background-color: #000000;
        position: relative;
      }

      .hamburger::after,
      .hamburger::before {
        position: absolute;
        content: "";
        height: 3px;
        width: 20px;
        background-color: #000000;
      }

      .hamburger::after {
        top: 7px;
        left: 0;
      }

      .hamburger::before {
        bottom: 7px;
        left: 0;
      }
    }
  }
}

.topList {
  display: none;
  width: 70%;
  margin: 0.5rem auto;
  padding-bottom: 0.3rem;
  font-size: 1.2rem;
  font-weight: 600;
  color: #ccc;
  justify-content: space-between;
  a:hover {
    border-bottom: 3px solid #00457c;
  }
  a.active {
    background-color: #00457c;
    color: #fff;
    padding: 2px 5px;

  }

  @media (min-width: 768px) {
    display: flex;
    box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.06);
  }
}

.navbar-expand-collapse {
  transition: all 0.1s ease-out;
  position: fixed;
  display: flex;
  margin: auto;
  width: 100%;
  height: 100%;
  top: 0;
  z-index: 999;

  .expand-wrapper {
    margin: auto;
    width: 70%;
    min-height: 100%;
    background: #fff;

    .expand-item-wrapper {
      padding: 1rem;
      font-weight: 600;

      .expand-item {
        display: flex;
        justify-content: space-around;
        align-items: center;
        margin: auto;
        width: 100%;
      }
    }

    .login,
    .logout {
      background: #f7f7f7;
    }
  }

  .expand-bottom {
    display: inline-block;
    width: 30%;
    height: 100%;
    background: #747373;
    opacity: 0.6;
  }

  @media (min-width: 768px) {
    display: none;
    height: 60px;
    width: 70%;
    margin: auto;

    .login,
    .logout {
      display: none;
    }

    .category-group {
      display: flex;
      justify-content: space-between;
      line-height: 60px;

      .expand-item {
        width: 100%;
        font-weight: 600;
        font-size: 1.2rem;
        color: #676666;
      }

      .expand-item:hover {
        color: #642f09;
      }
    }
  }
}
</style>