<template>
  <div class="signin-container">
    <div class="wrapper py-5">
      <Spinner v-if="isLoading === true" />
      <div class="title-wrapper text-center mb-4 d-flex justify-content-center">
        <ProcessSpinner v-if="isProcessing === true" />
        <div class="title-login" :class="{ selected: loginSelected }" @click.prevent.stop="loginSelect">
          
        </div>
        <span class="line"></span>
        <div class="title-register" :class="{ selected: registerSelected }" @click.prevent.stop="registerSelect">
          
        </div>
      </div>
      <ValidationObserver v-slot="{ invalid, handleSubmit }">
        <form class="w-40" v-if="loginSelected" @submit.prevent.stop="handleSubmit(loginSubmit)">
          <div class="login-wrapper">
            <ValidationProvider rules="required" v-slot="{ errors, classes }">
              <div class="form-label-group mb-2">
                <label for="email" hidden>email</label>
                <input v-model="email" id="email" name="email" type="email" :class="classes" class="form-control"
                  placeholder=" Email" />
                <span class="invalid-feedback d-flex">{{ errors[0] }}</span>
              </div>
            </ValidationProvider>
            <ValidationProvider rules="required" v-slot="{ errors, classes }">
              <div class="form-label-group mb-3">
                <label for="password" hidden>Password</label>
                <input v-model="password" id="password" name="" type="password" :class="classes"
                  class="form-control" placeholder="" />
                <span class="invalid-feedback d-flex">{{ errors[0] }}</span>
              </div>
            </ValidationProvider>
            <button class="btn btn-lg btn-primary btn-block mb-3" :class="{ disabled: invalid }" type="submit"
              v-if="loginSelected">
              
            </button>
            <button class="fb-login" @click.stop.prevent="fbLogin">
              <img src="../../public/photo/2021_Facebook_icon.svg.webp" alt="" style="width: 1.5rem; height:1.5rem;">
              <div>Facebook </div>
            </button>
          </div>
        </form>
      </ValidationObserver>
      <ValidationObserver v-slot="{ invalid, handleSubmit }">

        <form class="w-40" v-if="registerSelected" @submit.prevent.stop="handleSubmit(registerSubmit)">
          <div class="login-wrapper">
            <ValidationProvider rules="required" v-slot="{ errors, classes }">
              <div class="form-label-group mb-2">
                <label for="name" ></label>
                <input v-model="name" id="name" name="" type="text" :class="classes" class="form-control" placeholder=""
                   />
                <span class="invalid-feedback d-flex">{{ errors[0] }}</span>
              </div>
            </ValidationProvider>
            <ValidationProvider rules="required" v-slot="{ errors, classes }">
              <div class="form-label-group mb-2">
                <label for="email" ></label>
                <input v-model="email" id="email" name="email" type="email" :class="classes" class="form-control" placeholder=" Email"
                   />
                <span class="invalid-feedback d-flex">{{ errors[0] }}</span>
              </div>
            </ValidationProvider>
            <ValidationProvider rules="required" v-slot="{ errors, classes }">
              <div class="form-label-group mb-3">
                <label for="password" ></label>
                <input v-model="password" id="password" name="" type="password" :class="classes" class="form-control"
                  placeholder=""  />
                <span class="invalid-feedback d-flex">{{ errors[0] }}</span>
              </div>
            </ValidationProvider>
            <ValidationProvider :rules="{required: confirmPassword === '' ? true : false, confirmed:password}" v-slot="{ errors, classes }" tag="div">
              <div class="form-label-group mb-3">
                <label for="confirmPassword" ></label>
                <input v-model="confirmPassword" id="confirmPassword" name="" type="password"
                :class="classes" class="form-control" placeholder=""  />
                <span class="invalid-feedback d-flex">{{ errors[0] }}</span>
              </div>
            </ValidationProvider>
            <button class="btn btn-lg btn-primary btn-block mb-3" :class="{ disabled: invalid }" type="submit">
              
            </button>
          </div>
        </form>
      </ValidationObserver>
    </div>
  </div>
</template>

<script>
import authorizationAPI from "../apis/authorization";
import { Toast } from "./../utils/helpers";
import ProcessSpinner from "../components/ProcessSpinner.vue";
import Spinner from "../components/Spinner.vue";
import store from "../store/index"

export default {
  components: {
    ProcessSpinner,
    Spinner,
  },
  data() {
    return {
      name: "",
      email: "",
      password: "",
      confirmPassword: "",
      loginSelected: true,
      registerSelected: false,
      isProcessing: false,
      isLoading: false
    };
  },
  created() {

  },
  methods: {
    async loginSubmit() {
      try {
        this.isLoading = true
        if (!this.email || !this.password) {
          Toast.fire({
            icon: "warning",
            title: "",
          });
          return;
        }

        const response = await authorizationAPI.signIn({
          email: this.email,
          password: this.password,
        });
        const { data } = response;

        if (data.status === "error") {
          throw new Error(data.message);
        }
        localStorage.setItem("token", data.token);

        this.$store.commit('setCurrentUser', data.user)
        if (data.user.role === 'admin') {
          this.$router.push("/admin/products")
        } else {
          this.$router.push("/")
        }
      } catch (error) {
        this.isLoading = false
        this.password = "";
        Toast.fire({
          icon: "warning",
          title: error.message,
        });
      }
    },
    async registerSubmit() {
      try {
        this.isProcessing = true
        if (
          !this.name ||
          !this.email ||
          !this.password ||
          !this.confirmPassword
        ) {
          Toast.fire({
            icon: "warning",
            title: "",
          });
          return;
        }

        const response = await authorizationAPI.signUp({
          name: this.name,
          email: this.email,
          password: this.password,
          confirmPassword: this.confirmPassword,
        });
        const { data } = response;
        if (data.status === "error") {
          throw new Error(data.message);
        }
        localStorage.setItem("token", data.token);
        this.$router.go(0);
        this.isProcessing = false

      } catch (error) {
        this.isProcessing = false
        Toast.fire({
          icon: "warning",
          title: error.message,
        });
      }
    },
    fbLogin() {
      window.FB.getLoginStatus((response) => {
        if (response.status === 'connected') {
          window.FB.api(
            "/me",
            { fields: "name , email" },
            async function (response) {
              await store.dispatch("fetchFbUser", response)
            })
        } else {
          window.FB.login(
            function (response) {
              if (response.status === "connected") {
                window.FB.api(
                  "/me",
                  { fields: "name, email" },
                  async function (response) {
                    await store.dispatch("fetchFbUser", response)
                  }
                )
              } else {
                console.log('Login Failure')
              }
            },
            {
              scope: "email, public_profile",
              return_scopes: true,
            }
          )
        }
      });
    },
    registerSelect() {
      if (this.registerSelected === false) {
        this.loginSelected = false;
        this.registerSelected = true;
      }
    },
    loginSelect() {
      if (this.loginSelected === false) {
        this.loginSelected = true;
        this.registerSelected = false;

      }
    },
  },
};
</script>

