<template>
  <div class="col-md-6">
    
    <ValidationObserver v-slot="{ invalid, handleSubmit }">
      <form class="form-wrapper" v-if="stepper === 2" @submit.prevent="handleSubmit(submitForm)" >
        <ProcessSpinner v-if="isProcessing === true" />
        <ValidationProvider rules="required" v-slot="{ errors, classes }">
          <div class="form-group mt-3">
            <label for="name"></label>
            <input id="name" type="text" name="" v-model="name" class="form-control" :class="classes" placeholder="">
            <span class="invalid-feedback d-flex">{{ errors[0] }}</span>
          </div>
        </ValidationProvider>

        <ValidationProvider rules="required|email" name="email" v-slot="{ errors, classes }">
          <div class="form-group">
            <label for="email">Email</label>
            <input type="email" id="email" name="Email" class="form-control" :class="classes" placeholder=" Email"
              v-model="email" />
            <span class="invalid-feedback d-flex">{{ errors[0] }}</span>
          </div>
        </ValidationProvider>
        <ValidationProvider rules="required" v-slot="{ errors, classes }">
          <div class="form-group mt-3">
            <label for="phone"></label>
            <input id="phone" type="number" name="" v-model="phone" class="form-control" :class="classes" placeholder="">
            <span class="invalid-feedback d-flex">{{ errors[0] }}</span>
          </div>
        </ValidationProvider>
        <ValidationProvider rules="required" v-slot="{ errors, classes }">
          <div class="form-group">
            <label for="address"></label>
            <input type="text" id="address" name="" class="form-control" :class="classes" placeholder=""
              v-model="address" />
            <span class="invalid-feedback d-flex">{{ errors[0] }}</span>
          </div>
        </ValidationProvider>
        <input type="hidden" name="userId" :value="userId" />
        <input type="hidden" name="cartId" :value="cartId" />
        <input type="hidden" name="amount" :value="amount" />
        <input type="hidden" name="shipping_status" value="0" />
        <input type="hidden" name="payment_status" value="0" />
        
        <button type="submit" class="next-btn" :class="{disabled: invalid}"></button>
     
      </form>
    </ValidationObserver>
    <div id="app" class="container">
</div>
  </div>
</template>

<script>
import OrderApi from '../apis/order'
import { Toast } from '../utils/helpers'
import { mapState } from 'vuex';
import ProcessSpinner from './ProcessSpinner.vue';

export default {
  components: {
    ProcessSpinner
  },
  props: {
    initialStepper: {
      type: Number,
      required: true,
    },
    totalPrice: {
      type: Number,
      required: true,
    },

  },
  data() {
    return {
      userId: '',
      name: "",
      email: "",
      phone: "",
      address: "",
      shipping_status: 0,
      payment_status: 0,
      cartId: "",
      amount: this.totalPrice,
      stepper: this.initialStepper,
      orderId: '',
      isProcessing: false
    };
  },
  created() {
    this.cartId = localStorage.getItem('cartId')
    this.userId = this.currentUser.id
  },
  methods: {
    handleStepper(stepper) {
      this.stepper = this.initialStepper;
      if (this.stepper < 3) {
        this.stepper++;
        stepper = this.stepper;
      }
      this.$emit("click-step-btn", stepper);
    },
    async submitForm() {
      try {
        this.isProcessing = true
        const response = await OrderApi.postOrder({
          userId: this.userId,
          name: this.name,
          email: this.email,
          phone: this.phone,
          address: this.address,
          cartId: this.cartId,
          amount: this.amount,
          shipping_status: this.shipping_status,
          payment_status: this.payment_status
        })
        const { data } = response

        if (response.status === "error") {
          throw new Error(data.message);
        }
        this.stepper++
        this.$emit("click-step-btn", this.stepper);
        this.$bus.$emit('cartUpdate', {
          cart: []
        })
        this.isProcessing = false
      } catch (error) {
        this.isProcessing = false
        Toast.fire({
          icon: 'error',
          title: '...'
        })
      }


    },
  },
  watch: {
    initialStepper: function () {
      this.stepper = this.initialStepper;
    },
    totalPrice: function () {
      this.amount = this.totalPrice
    },
    currentUser: function () {
      this.userId = this.currentUser.id
    }
  },
  computed: {
    ...mapState(['currentUser', 'isAuthenticated'])
  }
};
</script>

<style lang="scss"></style>