<template>
  <div class="subtotal-wrapper mt-5 col-md-6 p-0" v-if="stepper < 3">
    <div class="wrapper-title"></div>
    <div class="info-wrapper">
      <div class="list-item">
        <div class="item-title"></div>
        <div class="price">NT$ {{ totalPrice }}</div>
      </div>
      <div class="list-item">
        <div class="item-title"></div>
        <div class="price"></div>
      </div>
    </div>
    <div class="total-price">
      <div class="title"></div>
      <div class="price">NT$ {{ totalPrice }}</div>
    </div>
    <div
      class="next-btn"
      v-if="stepper === 1"
      @click.prevent.stop="handleStepper(stepper)"
    >
      
    </div>
    <div
      class="prev-btn"
      v-if="stepper === 2"
      @click.prevent.stop="cancelStepper(stepper)"
    >
      
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      stepper: this.initialStepper,
    };
  },
  props: {
    initialStepper: {
      type: Number,
      required: true,
    },
    totalPrice: {
      type: Number,
      required: true,
    },
  },
  methods: {
    handleStepper(stepper) {
      if (this.stepper < 3) {
        this.stepper++;
        stepper = this.stepper;
      }
      this.$emit("click-step-btn", stepper);
    },
    cancelStepper(stepper) {
      if (this.stepper > 1) {
        this.stepper--;
        stepper = this.stepper;
      }
      this.$emit("click-step-btn", stepper);
    },
  },
  watch: {
    initialStepper: function () {
      this.stepper = this.initialStepper;
    },
  },
};
</script>
