import Vue from 'vue'
import Vuex from 'vuex'
import authorizationApi from '../apis/authorization'
import router from '../router/index'
import { Toast } from '../utils/helpers'


Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    currentUser: {
      id: -1,
      name: '',
      email: '',
      role: ''
    },
    isAuthenticated: false
  },
  getters: {
  },
  mutations: {
    setCurrentUser (state, currentUser) {
      state.currentUser = {
        ...state.currentUser, 
        ...currentUser 
      }
      state.isAuthenticated = true
    },
    revokeAuthentication (state) {
      state.currentUser = {}
      state.isAuthenticated = false
      localStorage.removeItem('token')
    }
  },
  actions: {
    async fetchCurrentUser({ commit }){
      try {
        const  {data} = await authorizationApi.getCurrentUser()
        if (data.status === "error") {
          throw new Error(data.message);
        }
        console.log('data', data.currentUser)
        
        commit('setCurrentUser', data.currentUser)
        return true
      } catch (error) {
        console.log('error',error.message)
        return false
      }
    },
    async fetchFbUser({ commit }, response) {
      try {
        const backEndResponse = await authorizationApi.postFbSignIn({
          name: response.name,
          email: response.email
        })
        if(backEndResponse.status === 'error') {
          throw new Error(backEndResponse.data.message)
        }
        localStorage.setItem('token', backEndResponse.data.token)
        commit('setCurrentUser', backEndResponse.data.user)
        router.push({ name: "products" })
    
      } catch (error) {
        Toast.fire({
          icon: 'error',
          title: ''
        })
        console.log(error)
      }
    }
  },
  modules: {
  }
})
