import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import Products from '../views/Products.vue'
import Signin from '../views/Signin.vue'
import Cart from '../views/Cart.vue'
import Order from '../views/Order.vue'
import Search from '../views/Search.vue'
import store from './../store'
import AdminDashboard from '../views/AdminDashboard.vue'
import NotFound from '../views/NotFound.vue'
import FrontPage from '../views/FrontPage.vue'
import AskedAndQuestions from '../views/AskedAndQuestions'

Vue.use(VueRouter)

const authorizeIsAdmin = (to, from, next) => {
  const currentUser = store.state.currentUser
  if (currentUser && (currentUser.role !== 'admin')) {
    next('/not-found')
    return
  }

  next()
}

const routes = [
  {
    path: '/',
    name: 'front',
    component: FrontPage,
    children: [
      {
        path: '/',
        name: 'home',
        component: HomeView
      },
      {
        path: '/products',
        name: 'products',
        component: Products
      },
      {
        path: '/products/:id',
        name: 'product',
        component: () => import('../views/Product.vue')
      },
      {
        path: '/signin',
        name: 'sign-in',
        component: Signin

      },
      {
        path: '/cart',
        name: 'cart',
        component: Cart

      },
      {
        path: '/order',
        name: 'order',
        component: Order
      },
      {
        path: '/search',
        name: 'search',
        component: Search

      },
      {
        path: '/asked_questions',
        name: 'asked_questions',
        component: AskedAndQuestions
      }
    ]
  },

  {
    path: '/admin',
    name: 'admin-dashboard',
    component: AdminDashboard,
    beforeEnter: authorizeIsAdmin,
    children: [
      {
        path: '/admin/products',
        name: 'admin-products',
        component: () => import('../views/dashboard/AdminProducts.vue'),
        beforeEnter: authorizeIsAdmin
      },
      {
        path: '/admin/categories',
        name: 'admin-categories',
        component: () => import('../views/dashboard/AdminCategories.vue'),
        beforeEnter: authorizeIsAdmin
      },
      {
        path: '/admin/orders',
        name: 'admin-orders',
        component: () => import('../views/dashboard/AdminOrders.vue'),
        beforeEnter: authorizeIsAdmin
      },
    ]
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },
  {
    path: '*',
    name: 'not-found',
    component: NotFound,
  }
]

const router = new VueRouter({
  routes
})

router.beforeEach(async (to, from, next) => {

  const token = localStorage.getItem('token')
  let isAuthenticated = false
  if (token) {
    isAuthenticated = await store.dispatch('fetchCurrentUser')
  }

  const pathsWithoutAuthentication = ['sign-in', 'products', 'product', 'search', 'home']
  if (!isAuthenticated && !pathsWithoutAuthentication.includes(to.name)) {
    next('/signin')
    return
  }
  if (isAuthenticated && to.name === 'sign-in') {
    next('/products')
    return
  }
  next()
})

export default router
